/* Li phim yu thch */
.wishlist-grid {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr); /* Mobile: 1 ct */
}

@media (min-width: 768px) {
  .wishlist-grid {
    grid-template-columns: repeat(3, 1fr); /* Tablet: 3 ct */
  }
}

@media (min-width: 1200px) {
  .wishlist-grid {
    grid-template-columns: repeat(6, 1fr); /* Desktop: 5 ct */
  }
}

/* Card poster */
.wishlist-card img {
  width: 100%;
  aspect-ratio: 2 / 3;
  object-fit: cover;
  border-radius: 8px;
  transition: transform 0.3s ease;
}

.wishlist-card img:hover {
  transform: scale(1.05);
}

.wishlist-card .title {
  display: -webkit-box;
  -webkit-line-clamp: 2;   /* hin th ti a 2 dng */
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  min-height: 2.6em;       /* gi chiu cao u gia cc card */
}

/* Nt xa */
.remove-btn {
  width: 28px;
  height: 28px;
  line-height: 28px;
  padding: 0;
}